<template>
  <div style="width: 100%; height: 100%; display: flex; flex-direction: column; justify-content: space-between;">
    <div style="flex: 1; position: relative;">
      <h3>
        Learning Effectiveness
        <span class="info-icon" title="This chart shows the assessment points versus additional time spent beyond the planned 60 minutes.">ℹ️</span>
      </h3>
      <p>Assessment points vs. Additional Time Spent on Activity</p>
      <canvas ref="scatterChart" style="width: 100%; height: 100%;"></canvas>
    </div>
    <!-- Manual Legend -->
    <div class="manual-legend">
      <div class="legend-item">
        <div class="legend-box" style="background-color: orange;"></div>
        <span>Improvement Potential</span>
      </div>
      <div class="legend-item">
        <div class="legend-box" style="background-color: green;"></div>
        <span>Best Practice</span>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, onMounted, ref } from "vue";
import { Chart, registerables } from "chart.js";

Chart.register(...registerables);

export default defineComponent({
  name: "ScatterPlot",
  setup() {
    const scatterChart = ref(null);
    const scatterData = ref([]);
    let chartInstance = null;

    const getCookie = (name) => {
      const match = document.cookie.match(new RegExp("(^| )" + name + "=([^;]+)"));
      return match ? match[2] : null;
    };

    const fetchData = async () => {
      try {
        const authToken = getCookie("auth_token");
        if (!authToken) {
          console.error("No authentication token found.");
          return;
        }

        const response = await fetch("http://localhost:8000/learningEffectiveness", {
          headers: { Authorization: `Bearer ${authToken}` },
        });

        if (!response.ok) throw new Error(`HTTP error! Status: ${response.status}`);

        const data = await response.json();
        console.log("API Response:", data);

        scatterData.value = data.activitiesEfficiency.flatMap(activity =>
          activity.scores.map(score => ({
            x: score.duration - 60, // Adjust duration to show time above 60 min
            y: score.score
          }))
        );

        console.log("Mapped Scatter Data:", scatterData.value);
        renderChart();
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    const renderChart = () => {
      const ctx = scatterChart.value?.getContext("2d");
      if (!ctx) {
        console.error("Failed to get canvas context.");
        return;
      }

      if (chartInstance) {
        chartInstance.destroy();
      }

      // Dynamically calculate x-axis range
      const xValues = scatterData.value.map(d => d.x);
      const xMin = Math.min(...xValues) - 5;
      const xMax = Math.max(...xValues) + 5;

      chartInstance = new Chart(ctx, {
        type: "scatter",
        data: {
          datasets: [
            {
              label: "Learning Effectiveness",
              data: scatterData.value,
              backgroundColor: "rgba(75, 192, 192, 1)",
              pointRadius: 6,
              pointHoverRadius: 8,
            },
          ],
        },
        options: {
          plugins: {
            legend: { display: false },
          },
          scales: {
            x: {
              type: "linear",
              title: {
                display: true,
                text: "Time Above Planned Time (minutes)",
                font: { size: 12, weight: "bold" },
              },
              min: xMin,
              max: xMax,
            },
            y: {
              title: {
                display: true,
                text: "Assessment Points",
                font: { size: 12, weight: "bold" },
              },
              min: 0,
              max: 100,
            },
          },
        },
      });
    };

    onMounted(fetchData);

    return { scatterChart };
  },
});
</script>

